<template>
	<div>
		<!-- nav 导航栏 -->
		<div class="nav_bar_class">
			<van-nav-bar
			title="网易严选"
			>
			    <van-icon name="wap-home-o" size="1rem" slot='left' @click='gotoHomeFn'/>
			    <van-icon name="shopping-cart-o" size="1rem" slot='right' />
			</van-nav-bar>
		</div>
		
		<!-- 显示商品图片 名字价格 -->
		<div class="goods_detail">
			<img :src='goods_img' />
			<p>{{goods_name}}</p>
			<p>{{goods_price}}}</p>
		</div>

		<!-- sku -->
		<div class="skuClass">
			<van-sku
				v-model="show_sku"
				:sku="sku"
				:goods="goods_sku"
				:goods-id="goodsId_sku"
				@add-cart='onSkuClickBuy'
				@buy-clicked='onSkuClickBuy'
			    >
			    <van-uploader 
			    v-model="fileList"
			    multiple  
			    :after-read="afterRead" 
			    slot='sku-actions-top'/>
			</van-sku>
		</div>
		

		<!-- 商品购买导航 -->
		<div class="goodsActionClass">
			<van-goods-action>
				<van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
				<van-goods-action-button type="warning" text="立即购买"  @click="onClickButton" />
				<van-goods-action-button type="danger" text="加入购物车"  @click="onClickButton" />
			</van-goods-action>
		</div>
	</div>
</template>

<script>
import '../assets/resetVant.css';
export default{
	name:'goods_detail',
	data(){
		return {
			// 图片预览数据
			fileList:[],
			// 上传的图片数据保存
			upLoaderFileArr:[],
			goods_name:'',
			goods_price:'',
			goods_img:'',
			goodsId_sku:0,
			show_sku:false,
			goods_sku:{
				// 默认商品 sku 缩略图
  				picture: ''
			},
			sku: {
				// 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
				// 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
				tree: [
				    {
					    k: '颜色', // skuKeyName：规格类目名称
					    k_s: 's1', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
					    v: [
					        {
					          id: 'color_1', // skuValueId：规格值 id
					          name: '粉色', // skuValueName：规格值名称
					          imgUrl: 'https://yanxuan-item.nosdn.127.net/0afcd9dd065aa207e56d34dd85aa136e.png', 
					          // 规格类目图片，只有第一个规格类目可以定义图片
					          previewImgUrl: 'https://yanxuan-item.nosdn.127.net/0afcd9dd065aa207e56d34dd85aa136e.png', 
					          // 用于预览显示的规格类目图片
					        },
					        {
					          id: 'color_2',
					          name: '蓝色',
					          imgUrl: 'https://yanxuan-item.nosdn.127.net/c552b8bf17d88aa9bba6972f1d449483.png',
					          previewImgUrl: 'https://yanxuan-item.nosdn.127.net/c552b8bf17d88aa9bba6972f1d449483.png',
					        }
					    ],
					    largeImageMode: true, //  是否展示大图模式
				    },
				    {
					    k: '尺寸', // 类目名称是尺寸
					    k_s: 's2', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
					    v: [
					        {
					          id: 'size_1', // skuValueId：规格值 id
					          name: 'S', // skuValueName：规格值名称
					          // 因为是尺寸类目所以不需要定义自定义图片 只有第一个类目可以自定义图片
					        },
					        {
					          id: 'size_2',
					          name: 'M',
					        },
					        {
					          id: 'size_3',
					          name: 'L',
					        }
					    ]
				    }
			    ],
			    // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
			    list: [
				    {
				      id: 2259, // skuId
				      s1: 'color_1', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_1', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 10000, // 价格（单位分）
				      stock_num: 10 // 当前 sku 组合对应的库存
				    },
				    {
				      id: 2259, // skuId
				      s1: 'color_1', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_2', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 12000, // 价格（单位分）
				      stock_num: 70 // 当前 sku 组合对应的库存
				    },
				    {
				      id: 2259, // skuId
				      s1: 'color_1', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_3', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 15000, // 价格（单位分）
				      stock_num: 90 // 当前 sku 组合对应的库存
				    },
				    {
				      id: 2259, // skuId
				      s1: 'color_2', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_1', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 10000, // 价格（单位分）
				      stock_num: 100 // 当前 sku 组合对应的库存
				    },
				    {
				      id: 2259, // skuId
				      s1: 'color_2', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_2', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 12000, // 价格（单位分）
				      stock_num: 20 // 当前 sku 组合对应的库存
				    },
				    {
				      id: 2259, // skuId
				      s1: 'color_2', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: 'size_3', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: 15000, // 价格（单位分）
				      stock_num: 200 // 当前 sku 组合对应的库存
				    },

			    ],
			    price: '1.00', // 默认价格（单位元）
			    stock_num: 227, // 商品总库存
			    collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
			    none_sku: false, // 是否无规格商品
			    messages: [
				    {
				      // 商品留言
				      datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
				      multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
				      name: '留言1', // 留言名称
				      type: 'text', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
				      required: '1', // 是否必填 '1' 表示必填
				      placeholder: '' // 可选值，占位文本
				    },
				    {
				      // 商品留言
				      datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
				      multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
				      name: '留言2', // 留言名称
				      type: 'date', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
				      required: '1', // 是否必填 '1' 表示必填
				      placeholder: '' // 可选值，占位文本
				    }
			    ],
			    hide_stock: false // 是否隐藏剩余库存
			},
		}
	},
	created(){
		let _n = this.$route.params.goodsInfo;
		this.goods_img = _n.goods_img;
		this.goods_name = _n.goods_name;
		this.goods_price = _n.goods_price;

		// sku商品缩略图
		this.goods_sku.picture = _n.goods_img;
	},
	methods:{
		// 跳转到主页
		gotoHomeFn(){
			this.$router.push('/',()=>{
				localStorage.setItem('inxState',0);
			});
		},
		onClickIcon(){

		},
		// 显示sku组件
		onClickButton(){
			this.show_sku = true;
		},
		// sku组件中点击立即购买
		onSkuClickBuy(_sku){
			console.log(_sku);
			console.log(this.upLoaderFileArr)
		},
		// 上传图片后会触发afterRead回调函数
		afterRead(_file){
			this.upLoaderFileArr.push(_file);
		}
	}
}
</script>

