			    </template>
			</van-tabbar-item>
			<van-tabbar-item to='/cateList'>
			    <span>分类</span>
			    <template #icon="props">
			    	<div :class="props.active ? icon.icon_active_2 :  icon.icon_inactive_2">
			    		
			    	</div>
			    </template>
			</van-tabbar-item>
			<van-tabbar-item>
			    <span>值得买</span>
			    <template #icon="props">
