			    	</div>
			    </template>
			</van-tabbar-item>
			<van-tabbar-item>
			    <span>购物车</span>
			    <template #icon="props">
			    	<div :class="props.active ? icon.icon_active_4 :  icon.icon_inactive_4">
			    	</div>
			    </template>
			</van-tabbar-item>
			<van-tabbar-item to='/login'>
