	</div>
</template>

<script>
export default{
	name:'footerBar',
	data(){
		return {
			active:0,
			icon:{
				icon_active_1:"icon_active_1 footerIcon",
				icon_inactive_1:'icon_inactive_1 footerIcon',

				icon_active_2:"icon_active_2 footerIcon",
				icon_inactive_2:'icon_inactive_2 footerIcon',

				icon_active_3:"icon_active_3 footerIcon",
				icon_inactive_3:'icon_inactive_3 footerIcon',

				icon_active_4:"icon_active_4 footerIcon",
				icon_inactive_4:'icon_inactive_4 footerIcon',

				icon_active_5:"icon_active_5 footerIcon",
				icon_inactive_5:'icon_inactive_5 footerIcon',
			}
		}
	},
	mounted(){
		this.active = Number(localStorage.getItem('inxState'));
	},
	methods:{
		switchColumnBtn(){
			let _inx = this.active;
			localStorage.setItem('inxState',_inx);
	    }
	},  
}
</script>

