<template>
	<div class="titleDiv">
		<van-row type="flex" justify="space-between" gutter="20">
		    <van-col span="21">
		    	<van-field
		    	    class="searchDiv"
		    	    @input="inputChangeFn"
		    	    @keyup.enter="submitSearchValueFn"
				    v-model="searchValue"
				    placeholder="搜索好物"
				    >
				    <van-icon name="search" size=".5rem" slot="left-icon"/>
				    <van-icon 
				        v-show='isSearchCloseBtnShow'
				        @click='clearSearchValue'
				        name="close" 
				        size=".5rem" 
				        slot="button"/>
			    </van-field>

		    </van-col>
		    <van-col span="3">
		    	<van-button class="cancelBtn" type="primary" size="mini">取消</van-button>
		    </van-col>
		</van-row>
	</div>
</template>

<script>
import axios from 'axios';
export default{
	name:'SearchPage',
	data(){
		return {
			searchValue: '',
			isSearchCloseBtnShow:false,
		}
	},
	created(){
	},
	watch:{
		// 监听searchValue的变化
		searchValue(){
			if(this.searchValue)
			{
				this.isSearchCloseBtnShow = true;
			}
			else
			{
				this.isSearchCloseBtnShow = false;
			}
		}
	},
	methods:{
		inputChangeFn(_v){
			// 其实这个输入变化的事件监听 和上面watch的保留一个就行了 因为都是输入框值变化这里也不用做啥
		},
		clearSearchValue(){
			this.searchValue='';
		},
		// @keydown（键盘按下时触发），@keypress(键盘按住时触发)，@keyup(键盘弹起)
		// 搜索框键盘弹起事件 enter
		submitSearchValueFn(){
			axios.get('http://localhost:3344/search',{
				params:{
					inputValue:this.searchValue
				}
			})
			.then( _d =>{
		     	console.log(_d.data)
		    });
		}
	}
}
</script>

<style>
.titleDiv{
	padding: .2rem .3rem .2rem .4rem;
}

.searchDiv{
	font-size: .4rem;
    height: .65rem;
    background-color: #ededed;
    color: #666;
    border-radius: .11rem;
    justify-content: center; 
    align-items: center;
    font-size: .377rem;
    display: flex;
}

.cancelBtn{
	width: .9rem;
    height: .53333rem;
    font-size: .32rem;
    line-height: .53333rem;
    text-align: center;
    color: #2c3e50;
    background: #fff;
    border-style: none;
    margin-top: .1rem;
}
</style>