;
// 下面是个匿名自执行函数
(function(win){
	var _doc = win.document;//获得document对象
	var _docEl = _doc.documentElement;//获得html整个文档
	// 获取meta元素,获取指定选择器对应的元素 
	var _metaEl = _doc.querySelector('meta[name="viewport"]');
	// 获取设备像素比
	var _dpr = win.devicePixelRatio;//2
	// 缩放比例
	var _scale = 1/_dpr;

	var tid;

	// 设置页面缩放比
	_metaEl.setAttribute("content", "initial-scale=.5, maximum-scale="+_scale+", minimum-scale="+_scale+", user-scalable=no viewport-fit=cover")
	_docEl.setAttribute('data-dpr',_dpr);

	// 刷新rem函数
	function refreshRem(){
		// 获取当前dom视口的宽度为750   因为设备视口的宽度为375=dom视口*scale 
		var _width = _docEl.getBoundingClientRect().width;

		// userAgent 属性是一个只读的字符串，声明了浏览器用于 HTTP 请求的用户代理头的值。
		var _ua = navigator.userAgent.toLowerCase();//把这一长串的值转化为小写

		// 判断机型,如果不是iPad 是手机
		if(!/ipad/.test(_ua))
		{
			// dom视口宽度 = 设备视口宽度*dpr
			// 如果设备视口宽度大于750 按照750处理
			if(_width/_dpr > 750)
			{
				_width = 750*_dpr;
			}
		}

		var rem = _width/10;
		_docEl.style.fontSize = rem + 'px';
	}

	refreshRem();

	// 添加事件侦听，页面缩放
	win.addEventListener('resize',function(){
		clearTimeout(tid);
		tid = setTimeout(refreshRem,300);

	},false);

	// 页面刷新
	win.addEventListener("pageshow",
    function(e) {
        if (e.persisted) {
            clearTimeout(tid);
            tid = setTimeout(refreshRem, 300)
        }
    },
    false);
	

})(window);


/*
visual viewport：可见视口，即屏幕宽度；
layout viewport：布局视口，即DOM宽度；
idea viewport：理想视口，使布局视口就是可见视口；

设备宽度（visual viewport）与DOM宽度（layout viewport），scale的关系是：
（visual viewport）= （layout viewport）* scale

dpr 设备像素比= 设备像素/dom像素
scale 缩放比例 = 1/dpr;

在苹果的规范中，meta viewport 有6个属性(暂且把content中的那些东西称为一个个属性和值)，如下：
width：设置layout viewport 的宽度，为一个正整数，或字符串"width-device"；
initial-scale：设置页面的初始缩放值，为一个数字，可以带小数；
minimum-scale：允许用户的最小缩放值，为一个数字，可以带小数；
maximum-scale：允许用户的最大缩放值，为一个数字，可以带小数；
height：设置layout viewport 的高度，这个属性对我们并不重要，很少使用；
user-scalable：是否允许用户进行缩放，值为"no"或"yes", no 代表不允许，yes代表允许；


/aaaa/.test('aaaaaaaabbbbbbbb')
结果true 这个是判断左边的字符串是否在右边能找到

*/