<template>
	<div>
		<!-- nav 导航栏 -->
		<div class="nav_bar_class">
			<van-nav-bar
			title="网易严选"
			>
			    <van-icon name="wap-home-o" size="1rem" slot='left' @click='gotoHomeFn'/>
			    <van-icon name="shopping-cart-o" size="1rem" slot='right' />
			</van-nav-bar>
		</div>
		<!-- 中间的图标 -->
		<div class="logoWrap">
			<img src="//yanxuan.nosdn.127.net/39c5e4583753d4c3cb868a64c2c109ea.png">
		</div>
		<!-- 登录按钮 -->
		<div class="btnWrap">
			<van-button 
			:icon='login_icon'
			type="primary" 
			color="#dd1a21" 
			plain 
			block
			@click='loginBtnClickFn'
			>
		        邮箱账号登录
		    </van-button>
		    <!-- 用户注册 -->
		    <van-button 
			type="primary" 
			color="#dd1a21" 
			block
			@click='registerBtnClickFn'
			>
		        用户注册
		    </van-button>

		    <span class="other" @click='otherLoginFn'>其他登录方式></span>
		</div>

		<!-- 用户登录 -->
		<div class="loginWrap" v-show='isLoginShow'>
			<input v-model='login_user_mail' type="text" placeholder="邮箱账号" />
			<input v-model='login_password' type="password" placeholder="密码" />
			<input type="button" style='border:0' value="登录" @click='onSubmitLoginData' >
		</div>

		<div class="registerWrap loginWrap" v-show='isRegisterShow'>
			<input v-model='user_mail_value' type="text" placeholder="注册邮箱账号" />
			<input v-model='password_value' type="password" placeholder="注册登录密码">
			<input type="button" style='border:0' value="注册" @click='onSubmitRegisterData'>
		</div>
	</div>
</template>
<script>
import login_icon from '../assets/login_icon.png'
export default{
	name:'login',
	data(){
		return {
			msg:'登录页面',
			login_icon:login_icon,
			// 登录是否显示
			isLoginShow:false,
			// 登录用户名 密码
			login_user_mail:'',
			login_password:'',
			// 注册是否显示
			isRegisterShow:false,
			// 注册用户名 密码
			user_mail_value:'',
			password_value:'',
			registerUserArr:[],
		}
	},
	mounted(){

			// 从本地存储获取注册信息
			let _str = localStorage.getItem('registerArr');
			this.registerUserArr = JSON.parse(_str);
			console.log(this.registerUserArr);
			
	},
	components:{},
	methods:{
		// 跳转到主页
		gotoHomeFn(){
			this.$router.push('/',()=>{
				localStorage.setItem('inxState',0);
			});
		},

		// 点击登录按钮
		loginBtnClickFn(){
			this.isLoginShow = true;
			this.isRegisterShow = false;
		},

		// 点击注册按钮
		registerBtnClickFn(){
			this.isLoginShow = false;
			this.isRegisterShow = true;
		},
		// 其他登录方式
		otherLoginFn(){
			this.isLoginShow = false;
			this.isRegisterShow = false;
		},
		// 点击登录 提交登录数据
		onSubmitLoginData(){

			// 查询是否在注册用户里面，在注册用户里面并且密码对 就可以登录
			let _isReg = false;//是否已经注册
			for( let i = 0; i < this.registerUserArr.length; i++)
			{
				if(this.registerUserArr[i].user_mail === this.login_user_mail &&
				   this.registerUserArr[i].psd === this.login_password)
				{
					//说明已经注册 所以可以成功登录
					_isReg = true;
					this.$dialog.alert({
						title: '登录成功！',
	                    message: '欢迎回来：'+this.login_user_mail+'你好',
					}).then(()=>{
						this.$router.push('/');
					});
					
					break;
				}
			}
			// 如果没有注册提示去注册
			if(!_isReg){
				this.$dialog.alert({
					title: '登录失败！',
                    message: '请先去注册',
				}).then(()=>{
					this.login_password = '';
					this.login_user_mail = '';
				});
			}
		},
		// 点击注册 提交注册数据
		onSubmitRegisterData(){
			let _n = {
				user_mail:this.user_mail_value,
				psd:this.password_value
			}

			let _is = false;
			this.registerUserArr.filter( _d => {
				if(_d.user_mail === _n.user_mail)
				{
					_is = true;
				}
			})

			if(!_is)
			{
				this.registerUserArr.push(_n);
				// 本地存储注册信息
				let _str = JSON.stringify(this.registerUserArr)
				localStorage.setItem('registerArr',_str);

				// 注册成功 可以登录了
				this.user_mail_value = '';
				this.password_value = '';
				this.isLoginShow = true;
			    this.isRegisterShow = false;
			}
			else
			{
				console.log('用户名重复')
			}

			
		}
	}
}
</script>

<style scoped>
.logoWrap{
	text-align: center;
	padding-top: 2.13333rem;
	padding-bottom: 3.09333rem;
}
.btnWrap{
	padding: 0 .53333rem;
    margin-bottom: 1rem;
}

.btnWrap .van-button--normal{
	font-size: .37333rem;
}
.btnWrap .van-button{
	margin-bottom: .3rem;
	height: 1rem;
}
.btnWrap .van-icon__image {
	width: .53333rem;
	height: .53333rem;
}

.btnWrap .other{
	font-size: .37333rem;
	text-align: center;
	display: block;
}

.loginWrap{
	text-align: center;
}

.loginWrap input {
	display: block;
	margin: .3rem auto;
	width: 4rem;
	height: .7rem;
	font-size: .37333rem;
	border: 0;
	border-bottom: 1px solid #666;
}
</style>